@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
	color: gray;
}

.btn {
	background-color: white;
	border: none;
	font-family: 'Montserrat';
	font-size: 0.8rem;
	height: 24px;
 	border-radius: 8px;
	box-shadow: 0 4px 12px 0px rgba(0,0,0,.25);
	padding: 26px;
	display: flex;
	flex-direction: row;
	align-items: center;
	align-content: center;
	justify-content: center;
	text-align: center;
	transition: color 0.2s;
}

.btn span {
	align-self: center!important;
}

.btn-nodrop {
	box-shadow: none;
	opacity: 1;
}

.btn-nodrop:hover {
	opacity: 0.5;
}

.btn-line:hover {
	opacity: 1;
}

.btn-sm {
	width: 24px;
}

.btn-md {
	width: 33%;
}

.btn-lg {
	width: 180px;
}

.btn-blue {
	background-color: #0984e3;
	color: white;
}

.btn-blue:hover {
	background-color: #2980b9;
}

.btn-red {
	background-color: #e74c3c;
	color: white;
}

.btn-red:hover {
	background-color: #c0392b;
}

button i {
	font-size: 1.3em!important;	
}

button:hover {
	cursor: pointer;
}

.input-box {
	border: 2px solid lightgray;
	/* color: lightgray; */
	border-radius: 8px;
	padding: 16px 12px;
	font-family: 'Montserrat';
}

.input-box:placeholder {
	color: lightgray;
}


form.new-todo {
	display: flex;
	justify-content: space-between;
	align-self: flex-start;
	padding: 16px 0px;
}

form.new-todo input {
	width: 29vw;
}

form.new-todo button {
	border: none;
	background-color: #0984e3;
	color: white;
	border-radius: 12px;
	width: 18%;
	box-shadow: 0 4px 12px 0px rgba(0,0,0,.25);
	font-family: 'Montserrat';
}

@media screen and (max-width: 960px) {
	form.new-todo {
		width: 90%;
		margin: 0 auto;
	}
	form.new-todo input {
		width: calc(100% - 128px);
	}
	form.new-todo button {
		width: 80px;
	}
}
.complete label {
	text-decoration: line-through;
}

.list {
	/* background-color: blue; */
	display: flexbox;
	/* flex-wrap: wrap; */
	flex-direction: row;
	justify-content: center;
	/* align-self: flex-end; */
	/* width: 100%; */
}

ul {
	list-style-type: none;
	flex-direction: row!important;
	margin: 0px!important;
	padding: 0px!important;
}

.todo-row {
	display: flex!important;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
	font-family: 'Montserrat';
}

.todo-row input {
	width: 24px;
	height: 24px;
	border-radius: 50%;
	justify-self: start;
	/* justify-self:  */
}

.todo-row button {
	font-size: 1.2rem;
	justify-self: end;
}

.todo-row label {
	/* display: block; */
	/* margin: 0 auto; */
	width: 40vw;
	font-size: 1.2em;
	/* background-color: red; */
	justify-self: center;
	text-align: left!important;
	padding: 16px 8px;
}

@media screen and (max-width: 960px) {
	.todo-row {
		width: 90%;
		margin: 0 auto;
		text-align: left;
		align-content: flex-end;
		justify-content: flex-start;
	}

	.todo-row label {
		width: 80vw;
	}
}

form.label-bar {
	margin: 0 auto;
	display: flex;
	width: 100%;
	justify-content: space-around;
	border-bottom: 2px solid lightgray;
	font-weight: bold;
}

form.label-bar button {
	position: relative;
	background-color: inherit;
	border: none;
	padding: 24px 0px;
	width: 20%;
	font-family: 'Montserrat';
	font-weight: 600;
	font-size: 0.9rem;
}

form.label-bar div {
	opacity: 0!important;
}

form.label-bar button {
	opacity: 1;
}

form.label-bar button:hover div {
	opacity: 1!important;
}

form.label-bar div {
	position: absolute;
	bottom: -4px;
	border-top: 3px solid #0984e3;
	border-radius: 3px;
	width: 100%;
	height: 4px;
	align-self: flex-end;
	opacity: 1;
	transition: all 0.2s;
}

form.label-bar div.active {
	opacity: 1!important;
}
html {
    margin: 0 auto;
    width: 40%;
	transition: all 1s;
}

header {
    text-align: center;
}

h1 {
    font-size: 2rem;
    font-family: 'Raleway';
    font-weight: 700;
}

.delete-all-cont {
	display: flex;
	justify-content: flex-end;
	padding: 16px 0px;
}

.delete-all {
    border: none;
    background-color: #e74c3c;
    color: white;
    border-radius: 12px;
    width: 32%;
    box-shadow: 0 4px 12px 0px rgba(0,0,0,.25);
    font-family: 'Montserrat';
	padding: 16px;
}

.icon {
    font-family: 'Material Icons'!important;
    background-color: white;
    border: 0px;
}

