.hidden {
    display: none!important;
}

.transparent {
	opacity: 0;
}

#delete-all-cont {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 16px;
}

.boosted {
	margin-top: 16px!important;
}

#delete-all {
	justify-self: flex-start!important;
	align-self: flex-start!important;
}

footer {
	box-shadow: -1px 0px 8px 5px white;
	background-color: white;
	text-align: center;
	width: 40%;
	color: gray;
	font-family: 'Montserrat';
	display: flex;
	justify-content: center;
	align-content: center;
	position: fixed;
	padding: 20px 0px;
	bottom: 0;
}

.empty-sign {
	display: flex;
	height: 320px;
	flex-direction: column;
	justify-content: center;
}

.empty-sign div {
	width: 320px;
	height: 200px;
	background-image: url(/todo-app/static/media/empty-image.9a83cb2a.jpg);
	background-size: 100% auto;
	background-repeat: no-repeat;
	align-self: center;
	-webkit-filter: grayscale();
	        filter: grayscale();
}

.empty-sign span {
	font-size: 1em;
	text-align: center;
	align-self: center;
	font-family: 'Lato';
	font-style: italic;
	color: gray;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

@media screen and (max-width: 960px) {
	html {
	  width: 100%;
	}
	#delete-all-cont {
		width: 95%;
	}
	footer {
		width: 100%;
		font-size: 20%;
	}
}
